import { extendObservable, runInAction } from 'mobx';
import projects from './projects.json';
//import { observer } from 'mobx-react';

class Store {
    constructor(){
        extendObservable(this, {
            aptitudes: [{title: "I love new challenges", description: "Challenges force you to learn, and doing it quickly it's ideal in a world where technology is constantly changing."}, {title: "I think time matters", description: "Commitment and dedication allows you to accomplish deadlines and obtain the best results."}, {title: "I feel analytical thinking is the way", description: "Understanding a problem through logical thinking helps you to anticipate obstacles and reach the goal."}, {title: "I believe in breaking limits", description: "Breaking limits allows you to achieve goals by overcoming any kind of challenge"}],
            projects: projects,
            projectPosition: 0
        });
    }
    
    setter(prop, val){
        runInAction(()=>{
            this[prop] = val;
        });
    }
}

const STORE = new Store();
export default STORE;