import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { observer } from 'mobx-react';
import './Menu.css';

const Menu = withRouter ( class Menu extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
    }

    componentDidMount(){
        console.log(this.props.location.pathname)
        let path = this.props.location.pathname;
        this.setState( {menuPos: path === '/'? 'intro' : path.split('/')[1] });
    }

    render(){
        return (
            <nav className='Menu center'>
                <ul className='row'>
                    {['intro', 'work', 'who'].map( item => 
                        <li key={item}
                            className={this.state.menuPos === item? 'selected' : ''}>
                            <Link to={item==='intro'? '/' : '/'+item}
                                onClick={ () => this.setState({menuPos: item})}>
                                {item.toLocaleUpperCase()}
                            </Link>
                        </li>
                    )}
                </ul>
            </nav>
        )
    }
})

export default observer(Menu);