import React, { useState, useEffect } from 'react';
import { observer } from 'mobx-react';
import './Aptitudes.css'
import STORE from '../../../stores/Store';

function Aptitudes() {
    const [count, setCount] = useState(0);

    useEffect(()=>{
        setTimeout(()=>{
            setCount(count < STORE.aptitudes.length - 1? (count + 1) : 0);
        }, 12000);
    });

    return (
        <section className='Aptitudes screen-size'>
            <h4>{STORE.aptitudes[count].title}</h4>
            <p>{STORE.aptitudes[count].description}</p>

            <div className='row tick-marks'>
                {STORE.aptitudes.map((elem, index) => {
                    return <span className={index === count ? 'selected' : ''} key={index} onClick={() => setCount(index)}></span>
                })}
            </div>

        </section>
    )
}

export default observer(Aptitudes);