import React from 'react';
import Sketch from 'react-p5';

export default class Skills extends React.Component {
    
    particles = [];

    setup = (app, canvasParentRef) => {
        let cnv = app.createCanvas(app.windowWidth,500);
        cnv.parent(canvasParentRef);
        cnv.id('canvas');
        console.log(canvasParentRef)

        this.SkillsCoder = ['JavaScript', 'React', 'React Native', 'Node.js', 'Html5', 'Handlebars', 'CSS', 'SCSS', 'Git', 'Java', 'Phyton', 'MongoDB', 'REST', 'TCP / UDP', 'VS code', 'Android', 'GitHub', 'Arduino', 'Eclipse', 'Processing'];
        this.SkillsDesigner = ['Photoshop', 'Premier Pro', 'After Effects', 'Ilustrator', 'Figma', 'Marvel', 'Adobe XD'];

        this.SkillsCoder.map(skill => this.particles.push(new Particle(app, skill, 'coder')));
        this.SkillsDesigner.map(skill => this.particles.push(new Particle(app, skill, 'designer')));
    }

    draw = app => {
        app.background('#000');
        this.particles.map((skill, i) => {
            skill.paint();
            skill.move();
            skill.dragged()
            skill.joinParticles(this.particles);
        });
    }

    render(){
        return <Sketch setup={this.setup} draw={this.draw} />
    }
}

class Particle {
    constructor(app, skill, type){
        this.app = app;
        this.skill = skill;
        this.type = type;
        this.x = app.random(0, app.width);
        this.y = app.random(0, app.height);
        this.r = app.random(2,8);
        this.xSpeed = app.random(-2,2);
        this.ySpeed = app.random(-1,1.5);
        this.moving = true;

        this.skillDiv = app.createDiv('').id('skillDescription');
        this.skillDiv.parent('canvasParent');
    }

    paint() {
        this.app.noStroke();
        this.app.fill('rgba(200,169,169,0.5)');
        this.app.circle(this.x,this.y,this.r);
        if( this.type === 'designer' ) {
            this.app.noFill();
            this.app.stroke('rgba(200,169,169,0.5)')
            this.app.circle(this.x,this.y,this.r+5);
        }
    }

    move() {
        if(this.moving){
            if(this.x < 0 || this.x > this.app.width)
                this.xSpeed*=-1;
            if(this.y < 0 || this.y > this.app.height)
                this.ySpeed*=-1;
            this.x+=this.xSpeed;
            this.y+=this.ySpeed;
        }
    }

    dragged() {
        if(this.app.dist(this.x, this.y, this.app.mouseX, this.app.mouseY) < 15) {
            this.moving = false;
            this.skillDiv.position(this.x, this.y);
            this.skillDiv.html(`
            <div>
                <p>${this.type}</p>
                <h5>${this.skill}</h5>
            </div>
            `);

        }else{
            this.moving = true;
            this.skillDiv.html('');
        }
    }

    joinParticles(particles) {
        particles.forEach(elem =>{
            if(this.app.dist(this.x,this.y,elem.x,elem.y) < 85) {
                this.app.stroke('rgba(255,255,255,0.04)');
                this.app.line(this.x,this.y,elem.x,elem.y);
            }
        });
    }
}