import React from 'react';
import { observer } from 'mobx-react';
import './Project.css'
import STORE from '../../stores/Store';

class Project extends React.Component {

    constructor (props) {
        super(props);
        this.state = {
            position : 0,
        }
    }

    render(){
        let proj = STORE.projects[this.state.position];
    return (
        <section className='Project'>
            <article className='Information screen-size row'>
                <div>
                    <h1>{proj.name}</h1>
                    <h5>{proj.description}</h5>

                    <a href={proj.link} target='_blank' rel="noopener noreferrer">Let’s see it in action</a>
                </div>

                <div>
                    <ul>Year
                        <li>{proj.year}</li>
                    </ul>

                    <ul>Rols
                        {proj.rols.map((elem, i) => {
                        return <li key={i}>{elem}</li>
                    })}
                    </ul>

                    <ul>Components
                        {proj.components.map((elem, i) => {
                        return <li key={i}>{elem}</li>
                    })}
                    </ul>
                </div>
            </article>

            <img className='fullWidth' src={'./images/'+proj.images + '0.png'} alt={proj.name}/>

            <div className='Comments row'>
                <h2>{proj.comments[0].title}</h2>
                <h5>{proj.comments[0].description}</h5>
            </div>

            <img className='fullWidth' src={'./images/'+proj.images + '1.png'} alt={proj.name} /> {//img o videillo
            }

            <article className='Features'>
                <h1>Features.</h1>

                {proj.features.map((elem, i) => {
                    return <div className='row' key={i} >
                            <img id={i%2 === 0 ? 'uno' : 'dos'} src={'./images/'+proj.images + 'F' + i + '.png'} alt={proj.name}/>
                            <div id={i%2 === 0 ? 'dos' : 'uno'}>
                                <h1>{elem.title}</h1>
                                <h5>{elem.description}</h5>
                            </div>
                        </div>
                })}
            </article>

            <img className='fullWidth' src={'./images/'+proj.images + '2.png'} alt={proj.name} />

            <article className='Typography'>
                <h1>Typography.</h1>
                <div className='row'>
                    <div>
                        <h3>{proj.typography.name}</h3>
                        <h5 className='temp1'>{proj.typography.description}</h5>
                        <a href='/contact'>A posibilitie of a link</a>

                        <h4>ABCDFGHIJKLNÑOPQRSTUVWXYZ.<br />
                            abcdfghijklnñopqrstuvwxyz.<br />
                            1234567890-_!”·</h4>
                    </div>
                    <img src='./images/4.png' alt={proj.name}/>
                </div>
            </article>

            <article className='Colors'>
                <h1>Colors.</h1>
                <div className='row'>
                    {
                        proj.color.map((p, i)=>
                        <div className='center' key={i}>
                            <div className='cuadrito' style={{backgroundColor: p.value}}></div>
                            <h5>{p.name}</h5>
                        </div>)
                    }
                </div>
            </article>

            <div className='Comments row'>
                <h2>{proj.comments[1].title}</h2>
                <h5>{proj.comments[1].description}</h5>
            </div>

            <article className='Thanks center colum'>
                <h1>Thanks.</h1>
                <div className='row'>
                    <p onClick={()=> {
                        this.setState(prev => ({position: prev.position > 0 ? prev.position - 1 : prev.position}));
                        window.scrollTo(0, 0);
                    }}>{'<'} Previous project</p>
                    <p onClick={()=> {
                            this.setState(prev => ({position: prev.position < 2 ? prev.position + 1 : prev.position}));
                            window.scrollTo(0, 0);
                    }}>Next project {'>'}</p>
                </div>
            </article>

        </section>
    );
}
}

export default observer(Project);