import React from 'react';
import Begin from './Begin/Begin';
import './Intro.css';
import Aptitudes from './Aptitudes/Aptitudes';
import { Link } from 'react-router-dom';

function Intro() {
    return (
        <div className='Intro'>
            <Begin/>
            <Aptitudes/>

            <section className='Recent-Work screen-size'>
                <h1>Recent Work.</h1>
                <p><Link to={'/Project'} >View All.</Link></p>
            </section>

            <section className='Lets-Talk screen-size' id='Lets-Talk'>
                <h1>Let's Talk.</h1>
                <p>If you like my work just send me a message or contact me through social media below.</p>
                <a target='blank' href='mailto:leidercalvo@gmail.com'>SEND</a>
            </section>
        </div>
    )
}

export default Intro;